import { Form } from './Form';
export class Contacts extends Form {
    constructor(container, events, actions) {
        super(container, events);
        if (actions.onClick) {
            this._submit.addEventListener('click', actions.onClick);
        }
        this.valid = false;
    }
    get phone() {
        return this.container.phone.value;
    }
    set phone(value) {
        this.container.phone.value = value;
    }
    get email() {
        return this.container.email.value;
    }
    set email(value) {
        this.container.email.value = value;
    }
    setNextToggle(state) {
        this.valid = state;
    }
}
