import { Component } from './base/Component';
import { createElement, ensureElement } from '../utils/utils';
export class ShoppingCart extends Component {
    constructor(container, actions) {
        super(container);
        this._items = ensureElement('.basket__list', this.container);
        this._price = this.container.querySelector('.basket__price');
        this._button = this.container.querySelector('.basket__button');
        this._itemIndex = this.container.querySelector('.basket__item-index');
        this.items = [];
        if (this._button)
            this._button.addEventListener('click', actions.onClick);
    }
    set items(items) {
        if (items.length)
            this._items.replaceChildren(...items);
        else {
            this._items.replaceChildren(createElement('p', {
                textContent: 'Ваша Корзина пуста!',
            }));
        }
    }
    setOrderButton(value) {
        if (value > 0)
            this.setDisabled(this._button, false);
        else
            this.setDisabled(this._button, true);
    }
    set price(price) {
        this.setText(this._price, price + ' cинапсов');
    }
    setOrderIndex() {
        const orderedList = this.container.querySelectorAll('.basket__item-index');
        orderedList.forEach((item, idx) => this.setText(item, idx + 1));
    }
}
