import { Component } from './base/Component';
import { ensureElement } from '../utils/utils';
export class Modal extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._closeButton = ensureElement('.modal__close', container);
        this._content = ensureElement('.modal__content', container);
        this._nextButton = container.querySelector('.card__button');
        this._closeButton.addEventListener('click', this.close.bind(this));
        this.container.addEventListener('click', this.close.bind(this));
        this._content.addEventListener('click', (event) => event.stopPropagation());
    }
    set content(value) {
        this._content.replaceChildren(value);
    }
    open() {
        this.addStyleClass(this.container, 'modal_active');
        this.events.emit('modal:open');
    }
    close() {
        this.removeStyleClass(this.container, 'modal_active');
        this.content = null;
        this.events.emit('modal:close');
    }
    toggleCartBtn(state) {
        this.setDisabled(this._nextButton, state);
    }
    render(data) {
        super.render(data);
        this.open();
        return this.container;
    }
}
